<template>

    <section class="toolbox-wrap">
        <div class="toolbox-inner">

            <h5 class="toolbox-title">文字樣式</h5>

            <div class="form-group">
                <label for="year-show">顯示文字</label>
                <input type="checkbox" id="year-show" name="year-show" 
                    :checked="initData.year_display" 
                    @change="changeYearDisplay">
            </div>

            <div class="accordion" id="accordionExample">
                <div class="accordion-item">
                    <h2 class="accordion-header">
                        <!-- <button class="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">文字</button> -->
                        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="false" aria-controls="collapseOne">文字</button>
                    </h2>
                    <!-- <div id="collapseOne" class="accordion-collapse collapse show" data-bs-parent="#accordionExample"> -->
                    <div id="collapseOne" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
                        <div class="accordion-body">
                            
                            <div class="form-group">
                                <label for="year-text">文字</label>
                                <input id="year-text" name="year-text" type="text" 
                                    v-model="initData.year_text" 
                                    @change="changeYearText"
                                    @keyup="changeYearText">
                            </div>

                            <div class="form-group">
                                <label for="year-color">文字顏色</label>
                                <input id="year-color" name="year-color" type="color" 
                                    v-model="initData.year_color" 
                                    @change="changeYearColor"
                                    @keyup="changeYearColor">
                            </div>

                            <div class="form-group">
                                <label for="year-font-size">字體大小</label>
                                <input name="year-font-size" min="12" max="100" type="range" 
                                    v-model="initData.year_font_size" 
                                    @change="changeYearSize"
                                    @keyup="changeYearSize">{{ initData.year_font_size }}px
                            </div>

                            <div class="form-group">
                                <label for="year-font-weight">字重</label>
                                <select name="year-font-weight" id="year-font-weight" 
                                    v-model="initData.year_font_weight" 
                                    @change="changeYearWeight">

                                    <option value="normal">normal</option>
                                    <option value="bold">bold</option>
                                    <option value="bolder">bolder</option>
                                    <option value="lighter">lighter</option>
                                    <option value="100">100</option>
                                    <option value="200">200</option>
                                    <option value="300">300</option>
                                    <option value="400">400</option>
                                    <option value="500">500</option>
                                    <option value="600">600</option>
                                    <option value="700">700</option>
                                    <option value="800">800</option>
                                    <option value="900">900</option>
                                </select>
                            </div>

                            <div class="form-group">
                                <label for="year-font-family-link">Google 字型 link</label>
                                <textarea id="year-font-family-link" name="year-font-family-link" cols="40" rows="2" 
                                    v-model="initData.year_font_family_link" 
                                    @keydown.enter.exact.prevent 
                                    @keyup.enter.exact="changeFontLink"
                                    @keyup="changeFontLink"></textarea>
                            </div>

                            <div class="form-group">
                                <label for="year-font-family-name">Google 字型 CSS 名稱</label>
                                <input id="year-font-family-name" name="year-font-family-name" type="text" 
                                    v-model="initData.year_font_family_name" 
                                    @change="changeYearFont"
                                    @keyup="changeYearFont">
                            </div>

                        </div>
                    </div>
                </div>
                <div class="accordion-item">
                    <h2 class="accordion-header">
                        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">文字背景</button>
                    </h2>
                    <div id="collapseTwo" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
                        <div class="accordion-body">

                            <div class="form-group">
                                <label for="year-bg-show">啟用</label>
                                <input type="checkbox" id="year-bg-show" name="year-bg-show" 
                                    :checked="initData.year_background_display" 
                                    @change="changeYearBackgroundDisplay">
                            </div>
                            
                            <div class="form-group">
                                <label for="year-bg-color">文字背景顏色</label>
                                <input type="color" id="year-bg-color" name="year-bg-color"
                                    v-model="initData.year_background_color" 
                                    @change="changeYearBackgroundColor">
                            </div>

                            <div class="form-group">
                                <label for="year-bg-style">文字背景形狀</label>
                                <select name="year-bg-style" id="year-bg-style" 
                                    v-model="initData.year_background_style" 
                                    @change="changeYearBackgroundStyle">

                                    <option value="rect">長方形</option>
                                    <option value="square">正方形</option>
                                    <option value="round">圓形</option>
                                    <option value="ellipse">橢圓形</option>
                                </select>
                            </div>

                            <div class="form-group">
                                <label for="year-bg-border">文字背景尺寸</label>
                                <input name="year-bg-border" min="10" max="500" type="range" 
                                    v-model="initData.year_background_w" 
                                    @change="changeYearBackgroundWidth"
                                    @keyup="changeYearBackgroundWidth">{{ initData.year_background_w }}px
                            </div>

                            <div class="form-group">
                                <label for="year-bg-border">文字背景邊框尺寸</label>
                                <input name="year-bg-border" min="0" max="100" type="range" 
                                    v-model="initData.year_background_border" 
                                    @change="changeYearBackgroundBorder"
                                    @keyup="changeYearBackgroundBorder">{{ initData.year_background_border }}px
                            </div>

                            <div class="form-group">
                                <label for="year-bg-border-color">文字背景邊框顏色</label>
                                <input type="color" id="year-bg-border-color" name="year-bg-border-color"
                                    v-model="initData.year_background_border_color" 
                                    @change="changeYearBackgroundBorderColor">
                            </div>

                            

                        </div>
                    </div>
                </div>
                <!-- <div class="accordion-item">
                    <h2 class="accordion-header">
                        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                            Accordion Item #3
                        </button>
                    </h2>
                    <div id="collapseThree" class="accordion-collapse collapse" data-bs-parent="#accordionExample">
                        <div class="accordion-body">
                            
                        </div>
                    </div>
                </div> -->
            </div>


            <h5 class="toolbox-title">輸出</h5>

            <div class="form-group">
                <label for="grid-gap">格子間隙</label>
                <input type="range" min="0" id="grid-gap" name="grid-gap"
                    v-model="initData.grid_gap" 
                    @change="changeGridGap">{{ initData.grid_gap }}px
            </div>

            <div class="form-group">
                <label for="grid-gap-color">格子間隙顏色</label>
                <input type="color" id="grid-gap-color" name="grid-gap-color"
                    v-model="initData.grid_gap_color" 
                    @change="changeGridGapColor">
            </div>

            <div class="form-group">
                <label for="output-size">輸出尺寸 (寬度)</label>
                <input type="number" min="1" id="output-size" name="output-size"
                    v-model="initData.output_size" 
                    @change="changeOutputSize">px
            </div>

            <div class="form-group">
                <button id="takeshot-btn" @click="getScreenshot">產生圖檔</button>
            </div>

        </div>
        
    </section>

</template>

<script>
import { toRef } from 'vue';

export default {
    name: 'SixteenGriderToolbox',
    props: ['data'],
    emits: [
        'eventChangeYearDisplay',
        'eventChangeYearText',
        'eventChangeYearColor',
        'eventChangeYearWeight',
        'eventChangeYearFont',
        'eventchangeFontLink',

        'eventChangeYearBackgroundDisplay',
        'eventChangeYearBackgroundColor',
        'eventChangeYearBackgroundStyle',
        'eventChangeYearBackgroundWidth',
        'eventChangeYearBackgroundBorder',
        'eventChangeYearBackgroundBorderColor',
        
        'eventChangeGridGap',
        'eventChangeGridGapColor',
        'eventChangeOutputSize',

        'eventGetScreenshot',
    ],

    setup(props, context) {
        // 從 props 來的數據
        const initData = toRef(props, 'data');

        // 數據
        


        // 方法

        function changeYearDisplay() {
            context.emit('eventChangeYearDisplay', !initData.value.year_display);
        }

        function changeYearText() {
            context.emit('eventChangeYearText', initData.value.year_text);
        }

        function changeYearColor() {
            context.emit('eventChangeYearColor', initData.value.year_color);
        }

        function changeYearWeight() {
            context.emit('eventChangeYearWeight', initData.value.year_font_weight);
        }

        function changeYearFont() {
            context.emit('eventChangeYearFont', initData.value.year_font_family_name);
        }

        // 改變匯入的字型
        function changeFontLink() {
            context.emit('eventchangeFontLink', initData.value.year_font_family_link);
        }

        

        // 背景

        function changeYearBackgroundDisplay() {
            context.emit('eventChangeYearBackgroundDisplay', !initData.value.year_background_display);
        }

        function changeYearBackgroundStyle() {
            context.emit('eventChangeYearBackgroundStyle', initData.value.year_background_style);
        }

        function changeYearBackgroundColor() {
            context.emit('eventChangeYearBackgroundColor', initData.value.year_background_color);
        }

        function changeYearBackgroundWidth() {
            context.emit('eventChangeYearBackgroundWidth', initData.value.year_background_w)
        }

        function changeYearBackgroundBorder() {
            context.emit('eventChangeYearBackgroundBorder', initData.value.year_background_border);
        }

        function changeYearBackgroundBorderColor() {
            context.emit('eventChangeYearBackgroundBorderColor', initData.value.year_background_border_color);
        }

        
        

        function changeGridGap() {
            context.emit('eventChangeGridGap', initData.value.grid_gap)
        }

        function changeGridGapColor() {
            context.emit('eventChangeGridGapColor', initData.value.grid_gap_color)
        }

        

        // 修改輸出尺寸
        function changeOutputSize() {
            context.emit('eventChangeOutputSize', initData.value.output_size)
        }


        function getScreenshot() {
            context.emit('eventGetScreenshot');
        }




        //---------------------------------------------
        // 將 rgb(red, green ,blue) 字串 轉換為 JSON物件
        // 來源: https://stackoverflow.com/questions/34980574/how-to-extract-color-values-from-rgb-string-in-javascript
        //---------------------------------------------
        // function getRGB(str) {
        //     var match = str.match(/rgba?\((\d{1,3}), ?(\d{1,3}), ?(\d{1,3})\)?(?:, ?(\d(?:\.\d?))\))?/);
        //     return match ? {
        //     red: parseInt(match[1]),
        //     green: parseInt(match[2]),
        //     blue: parseInt(match[3])
        //     } : {};
        // }

        //-----------------------
        // 將 0~255 轉換為 16進位
        // 來源: https://stackoverflow.com/questions/5623838/rgb-to-hex-and-hex-to-rgb
        //-----------------------
        // function componentToHex(c) {
        //     var hex = c.toString(16);
        //     return hex.length == 1 ? "0" + hex : hex;
        // }
        
        //-----------------------
        // 將 rgb 轉換為 hex
        // 來源: https://stackoverflow.com/questions/5623838/rgb-to-hex-and-hex-to-rgb
        //-----------------------
        // function rgbToHex(r, g, b) {
        //     return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
        // }


        return {
            initData,
            
            changeYearDisplay,
            changeYearText,
            changeYearColor,
            changeYearWeight,
            changeYearFont,
            changeFontLink,

            changeYearBackgroundDisplay,
            changeYearBackgroundStyle,
            changeYearBackgroundColor,
            changeYearBackgroundWidth,
            changeYearBackgroundBorder,
            changeYearBackgroundBorderColor,

            changeGridGap,
            changeGridGapColor,
            changeOutputSize,

            getScreenshot,
        }
    }
}
</script>