<template>

    <div class="sixteen-grider">

        <!-- choose template -->
        <SixteenGriderChooseTemplate
            v-if="!data.choosed"
            @eventChooseTemplate="chooseTemplate"
        />

        <template v-if="data.choosed">
            <!-- Body -->
            <SixteenGriderBody
                ref="bodyRef"
                :data="data"
            />

            <!-- Toolbox -->
            <SixteenGriderToolbox 
                :data="data"
                @eventChangeYearDisplay="changeYearDisplay"
                @eventChangeYearText="changeYearText"
                @eventChangeYearColor="changeYearColor"
                @eventChangeYearWeight="changeYearWeight"
                @eventChangeYearFont="changeYearFont"
                @eventchangeFontLink="changeFontLink"
                
                @eventChangeYearBackgroundDisplay="changeYearBackgroundDisplay"
                @eventChangeYearBackgroundColor="changeYearBackgroundColor"
                @eventChangeYearBackgroundStyle="changeYearBackgroundStyle"
                @eventChangeYearBackgroundWidth="changeYearBackgroundWidth"
                @eventChangeYearBackgroundBorder="changeYearBackgroundBorder"
                @eventChangeYearBackgroundBorderColor="changeYearBackgroundBorderColor"

                @eventChangeGridGap="changeGridGap"
                @eventChangeGridGapColor="changeGridGapColor"
                @eventChangeOutputSize="changeOutputSize"
                @eventGetScreenshot="getScreenshot"
            />

            <!-- Output -->
            <SixteenGriderOutput
                ref="outputRef"
                v-show="isShowOutput"
                @eventClosedModal="closedModal"
            />
        </template>

        

    </div>

</template>

<script>
// 引入 html2canvas
import html2canvas from 'html2canvas';

import { ref, reactive, onMounted, onUnmounted } from 'vue';

// components
import SixteenGriderToolbox from './SixteenGriderToolbox.vue';
import SixteenGriderOutput from './SixteenGriderOutput.vue';
import SixteenGriderBody from './SixteenGriderBody.vue';
import SixteenGriderChooseTemplate from './SixteenGriderChooseTemplate.vue'

export default {
    name: 'SixteenGrider',
    components: {
        SixteenGriderToolbox,
        SixteenGriderOutput,
        SixteenGriderBody,
        SixteenGriderChooseTemplate,
    },

    setup() {
        // 數據
        const data = reactive({
            // 版型
            choosed: false,  // 是否選擇完成
            ratio: '1-1',    // 比例
            count_x: 4,      // 橫向格數
            count_y: 4,      // 縱向格數

            // 文字
            year_display: true,  // 是否顯示
            
            year_text: '',
            year_color: '#000000',
            year_font_size: 35,
            year_font_weight: '400',

            year_background_display: true,            // 是否啟用文字背景
            year_background_color: '#ffa500',         // 背景顏色
            year_background_style: 'round',           // 背景形狀
            year_background_w: 100,                   // 寬度(px)
            year_background_border: 0,                // 背景邊框
            year_background_border_color: '#FFFFFF',  // 背景邊框顏色

            // 字體
            year_font_family_link: 'https://fonts.googleapis.com/css2?family=Vast+Shadow&display=swap',
            year_font_family_name: 'Vast Shadow',

            grid_gap: 0,  // 格子間隙
            grid_gap_color: '#FFFFFF',  // 格子間隙顏色
            output_size: 1024,  // 輸出尺寸
        })



        let isShowOutput = ref(false); // 是否顯示 SixteenGriderOutput 子組件


        // 元素
        let bodyRef = ref(null);  // SixteenGriderBody 子組件
        let outputRef = ref(null);  // SixteenGriderOutput 子組件
        


        // 方法



        // 選擇版型
        function chooseTemplate(template, ratio) {
            let tempArr = template.split('x');
            data.count_x = parseInt(tempArr[0]);
            data.count_y = parseInt(tempArr[1]);

            data.ratio = ratio;

            setTimeout(()=>{
                data.choosed = true;
            }, 100)
        }



        function changeYearDisplay(value) {
            data.year_display = value;
        }

        function changeYearText(value) {
            data.year_text = value;
        }

        function changeYearColor(value) {
            data.year_color = value;
        }

        function changeYearWeight(value) {
            data.year_font_weight = value;
        }

        function changeYearFont(value) {
            data.year_font_family_name = value;
        }

        // 改變匯入的字型
        function changeFontLink(url) {
            data.year_font_family_link = url;  // 更新資料

            // 替換 link 元素的 href
            document.querySelector('link[data-type="temp"]').href = data.year_font_family_link;
        }


        function changeYearBackgroundDisplay(value) {
            data.year_background_display = value;
        }

        function changeYearBackgroundStyle(value) {
            data.year_background_style = value;
        }

        function changeYearBackgroundColor(value) {
            data.year_background_color = value;
        }

        function changeYearBackgroundWidth(value) {
            data.year_background_w = value;
        }

        function changeYearBackgroundBorder(value) {
            data.year_background_border = value;
        }
        
        function changeYearBackgroundBorderColor(value) {
            data.year_background_border_color = value;
        }


        function changeGridGap(value) {
            data.grid_gap = value;
        }

        function changeGridGapColor(value) {
            data.grid_gap_color = value;
        }
        
        function changeOutputSize(value) {
            data.output_size = value;
        }


        // 產生圖檔
        function getScreenshot() {
            isShowOutput.value = true;
            // $(document).scrollTop(0);

            // 文字縮放率 (根據欲輸出的尺寸決定文字縮放率)
            let text_ratio = data.output_size / bodyRef.value.captureRef.offsetWidth;
            bodyRef.value.yearRef.style.transform = `scale(${Math.floor(text_ratio * 100) / 100})`;

            // 高度比例
            let h_ratioArr = data.ratio.split('/');
            let h_ratio = h_ratioArr[1] / h_ratioArr[0];
            
            // 修改容器尺寸
            bodyRef.value.captureRef.style.width = `${data.output_size}px`;
            bodyRef.value.captureRef.style.height = `${data.output_size * h_ratio}px`;


            // 清空內部元素
            outputRef.value.imgWrapRef.replaceChildren();  
    
            // 使用 html2canvas 將元素轉換成 canvas圖片
            html2canvas( bodyRef.value.captureRef ).then(canvas => {
                // 將canvas圖片插入畫面
                outputRef.value.imgWrapRef.append(canvas); // 放置產出的canvas圖片

                // 還原數值
                bodyRef.value.captureRef.style.width = '';
                bodyRef.value.captureRef.style.height = '';
                bodyRef.value.yearRef.style.transform = '';
            });

            // html2canvas( bodyRef.value.captureRef, {
            //     width: 1024,
            //     height: 1024,
            // } ).then(canvas => {
            //     // 將canvas圖片插入畫面
            //     outputRef.value.imgWrapRef.append(canvas);
            // });

        }

        // 關閉 modal
        function closedModal() {
            isShowOutput.value = false;
        }




        onMounted(()=>{
            // 設定預設值
            let today = new Date();
            data.year_text = today.getFullYear();

            // 在 head 匯入字型
            let link = document.createElement('link');
            link.type = 'text/css';
            link.rel = 'stylesheet';
            link.href = data.year_font_family_link;
            link.setAttribute('data-type', 'temp');
            document.head.appendChild(link);
        })

        onUnmounted(()=>{
            // 移除 head 匯入的字型
            document.querySelector('link[data-type="temp"]').remove();
        })


        return {
            data,
            isShowOutput,

            bodyRef,
            outputRef,


            changeYearDisplay,
            changeYearText,
            changeYearColor,
            changeYearWeight,
            changeYearFont,
            changeFontLink,

            changeYearBackgroundDisplay,
            changeYearBackgroundStyle,
            changeYearBackgroundColor,
            changeYearBackgroundWidth,
            changeYearBackgroundBorder,
            changeYearBackgroundBorderColor,

            chooseTemplate,

            changeGridGap,
            changeGridGapColor,
            changeOutputSize,

            getScreenshot,
            closedModal,
        }
    }
}
</script>